import React from 'react';
import style from './SignUpForm.module.scss';
import Popup from "reactjs-popup";


class SignUpForm extends React.Component {
    handleSubmit(event) {
        event.preventDefault();
    }
    render() {
        return <div id={style.registration__form}>
            <form onSubmit={this.handleSubmit} className={style.form}>
                <div className={style.form__data}>
                    <fieldset>
                        <legend>Name</legend>
                        <input
                            component="input"
                            type="text"
                            placeholder="Your name"
                        />
                    </fieldset>
                    <fieldset>
                        <legend>Email</legend>
                        <input
                            type="email"
                            placeholder="Your email"
                        />
                    </fieldset><fieldset>
                        <legend>Phone</legend>
                        <input
                            type="phone"
                            placeholder="+38 (___) ___ __ __"
                        />
                    </fieldset>
                </div>
                <div className={style.formInstruction__select}>
                    <fieldset className={style.select}>
                        <select>
                            <option>Select your position</option>
                            <option>Second item</option>
                            <option>Thurd item</option>
                        </select>
                    </fieldset>
                    <div className={style.uploadPhoto}>
                        <fieldset>
                            <label>
                                <span>Upload yuor photo</span>
                                <input
                                    type="file"
                                    name="file"
                                    accept='image/jpeg'
                                />
                                <strong>Upload</strong>
                            </label>
                        </fieldset>
                        <p className={style.uploadPhoto__description}>File format jpg up to 5MB, the minimum size of 70x70px</p>
                    </div>
                </div>
                <div className={style.btnSignUp}>
                    <Popup
                        trigger={<button type="submit">SignUp</button>}
                        modal
                        lockScroll
                        closeOnDocumentClick>
                        {close => (
                            <div className={style.popup_content}>
                                <h4>Congratulations</h4>
                                <p>You have successfully pased the registration</p>
                                <span onClick={close}>OK</span>
                            </div>
                        )}
                    </Popup>
                </div>
            </form>
        </div>
    }
}

export default SignUpForm;
