import React from 'react';
import style from './Users.module.scss';
import axios from 'axios';

class Users extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            users: [],
            itemsToshow: 6
        };
    };
    componentDidMount() {
        axios.get(`https://frontend-test-assignment-api.abz.agency/api/v1/users?count=100`)
            .then(res => {
                const users = res.data.users
                this.setState({ users: users.reverse() });
                console.log(res.data)
            })
    };
    showMoreNext = () => {
        this.setState(prevState => ({ itemsToshow: prevState.itemsToshow + 3 }))
    };

    render() {
        return <section className={style.users}>
            <div className={style.users__content}>
                <div className={style.users__head}>
                    <h2>{this.props.usersSection.h2}</h2>
                    <h4>{this.props.usersSection.h4}</h4>
                </div>
                <div className={style.users__items_row}>
                    {this.state.users.slice(0, this.state.itemsToshow).map(u => <div key={u.id} className={style.item}>
                        <div className={style.item_img}>
                            <img src={u.photo} alt="avatar" />
                        </div>
                        <div className={style.item_description}>
                            <h5>{u.name}</h5>
                            <div className={style.personal_data}>
                                <p className={style.position}>{u.position}</p>
                                <p className={style.email}>{u.email}</p>
                                <p className={style.phone}>{u.phone}</p>
                            </div>
                        </div>
                    </div>
                    )}
                </div>
                <div className={style.users__btn} >
                    <button onClick={this.showMoreNext}>Show more</button>
                </div>
            </div>
        </section>
    }
}
export default Users